import * as XLSX from 'xlsx';
import { useState, useCallback } from 'react';

import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import Stack from '@mui/material/Stack';
import Grid from '@mui/material/Unstable_Grid2';
import LoadingButton from '@mui/lab/LoadingButton';
import {
  Table,
  Select,
  Checkbox,
  MenuItem,
  TableRow,
  TableBody,
  TableCell,
  TextField,
  Typography,
  TableContainer,
} from '@mui/material';

import { paths } from 'src/routes/paths';
import { useRouter } from 'src/routes/hooks';

import axiosInstance, { endpoints } from 'src/utils/axios';

import { useGetCities, useGetCountries } from 'src/api';

import { Upload } from 'src/components/upload';
import Scrollbar from 'src/components/scrollbar';
import { useSnackbar } from 'src/components/snackbar';
import { useTable, TableHeadCustom, TableSelectedAction } from 'src/components/table';

// ----------------------------------------------------------------------
const TABLE_HEAD = [
  { label: 'country *', width: 'calc(100%/6)' },
  { label: 'city *', width: 'calc(100%/6)' },
  { label: 'name english *', width: 'calc(100%/6)' },
  { label: 'name arabic *', width: 'calc(100%/6)' },
  { label: 'description english', width: 'calc(100%/6)' },
  { label: 'description arabic', width: 'calc(100%/6)' },
];
export default function NewEditManyForm() {
  const router = useRouter();

  const table = useTable({ defaultOrderBy: 'code' });

  const { countriesData } = useGetCountries(); /// edit
  const { tableData } = useGetCities(); /// edit

  const { enqueueSnackbar } = useSnackbar();

  const [data, setData] = useState([]);

  const handleArabicInputChange = (index, event) => {
    const arabicRegex = /^[\u0600-\u06FF0-9\s!@#$%^&*_-]*$/;

    if (arabicRegex.test(event.target.value)) {
      setData((prev) => {
        const updated = [...prev];
        updated[index] = { ...updated[index], [event.target.name]: event.target.value };
        return updated;
      });
    }
  };

  const handleEnglishInputChange = (index, event) => {
    const englishRegex = /^[a-zA-Z0-9\s,@#$!*_\-&^%]*$/;

    if (englishRegex.test(event.target.value)) {
      setData((prev) => {
        const updated = [...prev];
        updated[index] = { ...updated[index], [event.target.name]: event.target.value };
        return updated;
      });
    }
  };

  const handleSelect = (index, event) => {
    setData((prev) => {
      const updated = [...prev];
      updated[index] = { ...updated[index], [event.target.name]: event.target.value };
      return updated;
    });
  };

  const handleSelectMany = (event) => {
    setData((prev) => {
      const updated = [...prev];

      table.selected.forEach((item) => {
        updated[item] = { ...updated[item], [event.target.name]: event.target.value };
      });

      return updated;
    });
  };

  const handleDrop = useCallback((acceptedFiles) => {
    const file = acceptedFiles[0];

    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const results = e.target.result;
        const workbook = XLSX.read(results, { type: 'binary' });

        const workSheets = workbook.Sheets[workbook.SheetNames[0]];

        const jsonData = XLSX.utils.sheet_to_json(workSheets);
        setData(jsonData);
      };
      reader.readAsBinaryString(file);
    }
  }, []);

  const handleCreate = async () => {
    const isFormValid = data.every(
      (one) => one.name_english && one.name_arabic && one.country && one.city
    );

    if (!isFormValid) {
      alert('Please fill in all required fields.');
      return;
    }
    try {
      await axiosInstance.post(endpoints.hospitals.many, data);
      router.push(paths.superadmin.tables.hospitallist.root); /// edit
    } catch (e) {
      enqueueSnackbar(e, { variant: 'error' });
    }
  };

  return (
    <Grid container spacing={3}>
      <Grid xs={12} maxWidth="md">
        <Card sx={{ p: 3 }}>
          <Box
            sx={{
              px: 5,
              pb: 2,
            }}
          >
            <Upload
              name="excel"
              accept=".xlsx, .xls"
              label="upload excel"
              onDrop={(info) => handleDrop(info)}
            />
          </Box>
          {data.length > 0 && (
            <TableContainer>
              <TableSelectedAction
                numSelected={table.selected.length}
                rowCount={data.length}
                onSelectAllRows={(checked) =>
                  table.onSelectAllRows(
                    checked,
                    data.map((row, index, idx) => index)
                  )
                }
                action={
                  <>
                    <Typography sx={{ fontSize: 14, fontWeight: 600, pr: 1 }}>Country:</Typography>
                    <Select
                      variant="filled"
                      sx={{ width: '20%', border: '1px solid gray' }}
                      size="small"
                      name="country"
                      label="country"
                      onChange={handleSelectMany}
                    >
                      {countriesData.map((country, idx) => (
                        <MenuItem key={idx} value={country._id}>
                          {country.name_english}
                        </MenuItem>
                      ))}
                    </Select>
                    <Typography sx={{ fontSize: 14, fontWeight: 600, pr: 1 }}>City:</Typography>
                    <Select
                      variant="filled"
                      sx={{ width: '20%', border: '1px solid gray' }}
                      size="small"
                      name="city"
                      label="city"
                      onChange={handleSelectMany}
                    >
                      {tableData.map((city, idx) => (
                        <MenuItem key={idx} value={city._id}>
                          {city.name_english}
                        </MenuItem>
                      ))}
                    </Select>
                  </>
                }
                color="primary"
              />

              <Scrollbar>
                <Table size="medium">
                  <TableHeadCustom
                    headLabel={TABLE_HEAD}
                    rowCount={data.length}
                    numSelected={table.selected.length}
                    onSelectAllRows={(checked) =>
                      table.onSelectAllRows(
                        checked,
                        data.map((row, index, idx) => index)
                      )
                    }
                  />

                  <TableBody>
                    {data.map((one, index) => (
                      <TableRow key={index} hover selected={table.selected.includes(index)}>
                        <TableCell padding="checkbox">
                          <Checkbox
                            checked={table.selected.includes(index)}
                            onClick={() => table.onSelectRow(index)}
                          />
                        </TableCell>

                        <TableCell align="center">
                          <Select
                            variant="filled"
                            required
                            value={one.country || ''}
                            onChange={(e) => handleSelect(index, e)}
                            sx={{ width: '80%' }}
                            size="small"
                            name="country"
                          >
                            {countriesData.map((country, idx) => (
                              <MenuItem key={idx} value={country._id}>
                                {country.name_english}
                              </MenuItem>
                            ))}
                          </Select>
                        </TableCell>

                        <TableCell align="center">
                          <Select
                            variant="filled"
                            required
                            value={one.city || ''}
                            onChange={(e) => handleSelect(index, e)}
                            sx={{ width: '80%' }}
                            size="small"
                            name="city"
                          >
                            {tableData.map((city, idx) => (
                              <MenuItem key={idx} value={city._id}>
                                {city.name_english}
                              </MenuItem>
                            ))}
                          </Select>
                        </TableCell>

                        <TableCell align="center">
                          <TextField
                            size="small"
                            variant="filled"
                            lang="ar"
                            onChange={(e) => handleEnglishInputChange(index, e)}
                            value={one.name_english}
                            name="name_english"
                          />
                        </TableCell>
                        <TableCell align="center">
                          <TextField
                            size="small"
                            variant="filled"
                            lang="ar"
                            onChange={(e) => handleArabicInputChange(index, e)}
                            value={one.name_arabic}
                            name="name_arabic"
                          />
                        </TableCell>
                        <TableCell align="center">
                          <TextField
                            size="small"
                            variant="filled"
                            lang="ar"
                            onChange={(e) => handleEnglishInputChange(index, e)}
                            value={one.description}
                            name="description"
                          />
                        </TableCell>
                        <TableCell align="center">
                          <TextField
                            size="small"
                            variant="filled"
                            lang="ar"
                            onChange={(e) => handleArabicInputChange(index, e)}
                            value={one.description_arabic}
                            name="description_arabic"
                          />
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </Scrollbar>
            </TableContainer>
          )}

          {data.length > 0 && (
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton onClick={handleCreate} tabIndex={-1} variant="contained">
                Create All
              </LoadingButton>
            </Stack>
          )}
        </Card>
      </Grid>
    </Grid>
  );
}
